import { Component, NgModule } from '@angular/core';
import {HttpModule , Http, RequestOptions, Headers} from '@angular/http';
import 'rxjs/add/operator/map';
import {BackendService} from '../backend/backend.service';
import {Observable} from 'rxjs/Observable';
import 'rxjs/add/observable/of';
import {EventsService} from '../events.service';

@Component({
  selector: 'app-points',
  templateUrl: './points.component.html',
  styleUrls: ['./points.component.css']
})
export class PointsComponent {
  searchForm: any = {};
  points = null;
  openedPoint = -1;
  name = '';
  phone = '';
  pointsAreLoading = false;
  constructor(private eventsService: EventsService,
              private backendService: BackendService) {
    eventsService.on('startLoadPoints', () => {
      this.points = [];
      this.pointsAreLoading = true;
      this.openedPoint = -1;
    });
    eventsService.on('endLoadPoints', (res, form) => {
      this.pointsAreLoading = false;
      this.points = res.data.items;
      this.searchForm = form;
      this.addPricesToPoints();
    });
  }

  addPricesToPoints() {
    this.points.forEach(point => {
      point.distance = parseFloat(point.distance).toFixed(1);
      point.delivery = parseInt(point.delivery, 10);
      point.materialPrice = Math.round(parseFloat(point.price) * parseFloat(this.searchForm.count));
      point.fullPrice = Math.round(point.materialPrice + parseFloat(point.delivery));
    });
  }

  onPointClick(index) {
    this.openedPoint = index;
    this.eventsService.broadcast('openPoint', this.points[index], this.searchForm);
  }

  onOrderClick(point) {
    let form = {
      complect: point.item.id,
      count: this.searchForm.count,
      unit: this.searchForm.unit,
      address: this.searchForm.address.name,
      _lat: this.searchForm.lat,
      _lng: this.searchForm.lng,
      member: this.name,
      phone: this.phone
    };
    this.backendService.addOrder(point.id, form)
      .subscribe((res) => {
        if (res.success) {
          window.alert('Заявка успешно добавлена');
        } else {
          window.alert(res.data.message);
        }
      });
  }
}



