import { Component, NgModule } from '@angular/core';
import {HttpModule , Http, RequestOptions, Headers} from '@angular/http';
import 'rxjs/add/operator/map';
import {BackendService} from '../backend/backend.service';
import {Observable} from 'rxjs/Observable';
import 'rxjs/add/observable/of';
import {EventsService} from '../events.service';

@Component({
  selector: 'app-map',
  templateUrl: './map.component.html',
  styleUrls: ['./map.component.css']
})
export class MapComponent {
  map: any;
  ymaps: any;
  currentPoints = [];
  currentRoute: any;
  currentDeliveryPoint: any;
  constructor(private eventsService: EventsService) {
    this.ymaps = window['ymaps'];
    this.initMap();
    eventsService.on('endLoadPoints', (res, form) => {
      this.drawPoints(res.data.items);
    });
    eventsService.on('startLoadPoints', () => {
      this.removeAllPoints();
    });
    eventsService.on('openPoint', (point, searchForm) => {
      this.drawRoute([point.lat, point.lng], [searchForm.lat, searchForm.lng]);
    });
    eventsService.on('getDeliveryAddress', (point) => {
      this.drawDeliveryPoint(point);
    });
  }
  initMap() {
    document.addEventListener('DOMContentLoaded', event => {
      document.getElementById('map').style.width = window.innerWidth + 'px';
      document.getElementById('map').style.height = window.innerHeight + 'px';
      this.ymaps.ready(() => {
        this.map = new this.ymaps.Map('map', {
          center: [55.76, 37.64],
          zoom: 8
        });
      });
    });
  }
  drawDeliveryPoint(coors: number[]) {
    this.map.geoObjects.remove(this.currentDeliveryPoint);
    this.currentDeliveryPoint = new this.ymaps.Placemark(coors, {}, {
      preset: 'islands#redIcon'
    });
    this.map.geoObjects.add(this.currentDeliveryPoint);
  }
  drawPoints(items: any[]) {
    items.forEach(item => {
      let newPoint = new this.ymaps.GeoObject({
        geometry: {
          type: 'Point',
          coordinates: [item.lat, item.lng]
        },
        properties: {
          hintContent: item.address
        }
      });
      this.currentPoints.push(newPoint);
      this.map.geoObjects.add(newPoint);
    });
  }

  removeAllPoints() {
    this.currentPoints.forEach(point => {
      this.map.geoObjects.remove(point);
    });
    this.currentPoints = [];
  }

  drawRoute(startPoint, endPoint) {
    this.map.geoObjects.remove(this.currentRoute);
    this.ymaps.route([startPoint, endPoint]).then(
      (route) => {
        this.currentRoute = route;
        this.map.geoObjects.add(route);
        /*this.map.setBounds([startPoint, endPoint], {
          checkZoomRange: true,
        });*/
      },
      (error) => {
        alert('Возникла ошибка: ' + error.message);
      }
    );
  }
}



