import { BrowserModule } from '@angular/platform-browser';
import {Component, Injectable, NgModule} from '@angular/core';
import {HttpModule , Http, RequestOptions, Headers, URLSearchParams } from '@angular/http';
import 'rxjs/add/operator/map';
import {Observable} from 'rxjs/Observable';

@Injectable()
export class BackendService {
  API_HOST = 'http://endpoint.fasttruck.jlabs.pro/v1/';
  // API_HOST = 'http://localhost:3000/';

  constructor(private http: Http) {}

  public getComplects(): Observable<any> {
    return this.fetch('complect', this.getOptions());
  }

  public getAddress(query, meta): Observable<any> {
    const params = new URLSearchParams();
    params.set('query', query);
    params.set('city', 'Москва');
    params.set('meta', meta);
    params.set('limit', '1');
    return this.fetch('address', this.getOptions(params));
  }

  public getAddresses(query): Observable<any> {
    const params = new URLSearchParams();
    params.set('query', query);
    params.set('city', 'Москва');
    params.set('limit', '10');
    return this.fetch('address', this.getOptions(params));
  }

  public getPoints(form): Observable<any> {
    const params = new URLSearchParams();
    params.set('_lat', form._lat);
    params.set('_lng', form._lng);
    params.set('complect', form.complect);
    params.set('count', form.count);
    params.set('unit', form.unit);
    return this.fetch('point', this.getOptions(params));
  }

  public addOrder(id, form): Observable<any> {
    return this.post('point/' + id, form, this.getOptions());
  }

  private fetch(url: string, options): Observable<any> {
      return this.http.get(this.API_HOST + url, options)
        .map(res => res.json());
  }

  private post(url: string, body, options): Observable<any> {
    return this.http.post(this.API_HOST + url, body, options)
      .map(res => res.json());
  }

  private getOptions(params?: any): RequestOptions {
    const headers = new Headers();
    headers.append('Content-Type', 'application/json');
    headers.append('Accept', 'application/json');
    headers.append('Authorization', 'Bearer XOmXRO96iKPNeGRIImkZn4OtIs7FOvZu');
    return new RequestOptions({ headers: headers, params: params });
  }
}



