import { Component, NgModule } from '@angular/core';
import {HttpModule , Http, RequestOptions, Headers} from '@angular/http';
import 'rxjs/add/operator/map';
import {BackendService} from '../backend/backend.service';
import {Observable} from 'rxjs/Observable';
import 'rxjs/add/observable/of';
import {EventsService} from '../events.service';

@Component({
  selector: 'app-search-form',
  templateUrl: './searchForm.component.html',
  styleUrls: ['./searchForm.component.css']
})
export class SearchFormComponent {
  complectsArray = [];
  form = {
    address: null,
    lat: null,
    lng: null,
    complect: '',
    count: null,
    unit: 'т',
  };

  constructor(http: Http,
              private backendService: BackendService,
              private eventsService: EventsService) {
    backendService.getComplects()
      .subscribe((res) => this.complectsArray = res.data.items);
  }

  getAddresses = (keyword: any): Observable<any[]> => {
    if (keyword) {
      return this.backendService.getAddresses(keyword)
        .map(this.mapAddresses);
    } else {
      return Observable.of([]);
    }
  }

  onAddressChoose(value) {
    this.backendService.getAddress(value.name, value.meta)
      .subscribe(res => {
        if (res.data.items && res.data.items[0]) {
          this.form.lat = res.data.items[0].lat;
          this.form.lng = res.data.items[0].lng;
          this.eventsService.broadcast('getDeliveryAddress', [this.form.lat, this.form.lng]);
        }
      });
  }

  mapAddresses(res): any[] {
    const items = res.data.items;
    items.forEach((item) => {
      item.latlng = {lat: item.lat, lng: item.lng};
    });
    return items;
  }

  addressListFormatter = (data: any) => data.name;

  addressValueFormatter = (data: any) => data.name;

  onSubmit() {
    const request = {
      _lat: this.form.lat,
      _lng: this.form.lng,
      complect: this.form.complect,
      count: this.form.count,
      unit: this.form.unit,
    };
    this.eventsService.broadcast('startLoadPoints');
    this.backendService.getPoints(request)
      .subscribe((res) => this.eventsService.broadcast('endLoadPoints', res, this.form));
  }
}



